import { Component, OnInit } from '@angular/core';

@Component({

  selector: 'app-app-struct',
  templateUrl: './app-struct.component.html',
  styleUrls: ['./app-struct.component.css']
})
export class AppStructComponent implements OnInit {

  forgot=true;
  False=false;
  arrSubjects = ['AngularJS', 'Angular2', 'Angular3'];

  arrMembers = [
    {id: 1, name: 'Ha', active: true, },
    {id: 2, name: 'Ha2', active: false, },
    {id: 3, name: 'Ha3', active: true, },
    {id: 4, name: 'Ha4', active: false, },
    {id: 5, name: 'Ha5', active: true, },
    {id: 6, name: 'Ha6', active: false, },
  ];

  newNameMember = '';

  toggleForgot() {
    this.forgot = !this.forgot;
  }

  toggleAddMember(){
    this.arrMembers.unshift({
      id: this.arrMembers.length + 1,
      name: this.newNameMember,
      active: false

    });
    this.newNameMember= '';
    this.False = !this.False;
  }

  
  toggleDeleteMember(id: number){
  const index = this.arrMembers.findIndex(object => object.id === id);
  this.arrMembers.splice(index, 1); 
  }


  filterStatus= "filter_all";
  toggleFilterMember(active: boolean){
    const filterAll=  this.filterStatus === 'filter_all';
    const filterActive = this.filterStatus ==='filter_active' && active;
    const filterDeactive = this.filterStatus === 'filter_deactive' && !active;
    return filterAll || filterActive || filterDeactive;
  }
  constructor() { }
  
  ngOnInit() {
    
  }

}
