import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-list-person',
  template: `
          <app-person (myClick)=Volumn($event);></app-person>
          <h3>{{value}}</h3>
  `,
})
export class ListPersonComponent implements OnInit {
  value = 0;

  Volumn(isAdd: boolean) {
    if (isAdd) {
      this.value++;
    } else {
      this.value--;
    }
  }
  constructor() { }

  ngOnInit() {
  }

}
